.game-over {
  height: 100%;
  width: 100%;
  background-color: var(--navyBlue);
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  position: inherit;
}

.game-over span span {
  color: var(--aquaGreen);
}